export default {
	data() {
		return {
			/* 批量编辑 */
			isEdit: false,
			isSelectAll: false,
			isSelectPair: false,
			/* 列表 */
			cartList: [{
					test1: 1,
					test2: false,
					test3: false
				}, {
					test1: 4,
					test2: false,
					test3: false
				}, {
					test1: 100,
					test2: false,
					test3: false
				},
				{
					test1: 4,
					test2: false,
					test3: false
				},
				{
					test1: 4,
					test2: false,
					test3: false
				},
			],
			/* 模糊窗口 */
			modalName: null,
			/* 测试 */
			test1: 1
		}
	},
	methods: {
		/* 批量编辑 */
		edit() {
			this.isEdit = !this.isEdit
		},
		selectAll() {
			this.isSelectAll = !this.isSelectAll
		},
		selectPair(item, index) {
			item.test2 = !item.test2
		},
		selectOne(item, index) {
			item.test3 = !item.test3
		},
		buy() {
			uni.navigateTo({
				url: '/pages/cart/order-confirm'
			})
		},
		deleteItem() {

		},
		/* 单个编辑 */
		add(item, index) {
			item.test1 += 1
		},
		subtract(item, index) {
			if (item.test1 > 1) {
				item.test1 -= 1
			}
		},
		/* 模糊窗口 */
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		/* 页面跳转 */
		jump2item() {
			uni.navigateTo({
				url: '/pages/item/index'
			})
		},
		jump2shopsDetail() {
			uni.navigateTo({
				url: '/pages/shops/detail'
			})
		}
	}
}
